.App {
    text-align: center;
  }
  body{
      height: 100vh;
     
background: #feffff; /* Old browsers */
background: -moz-linear-gradient(top,  #feffff 0%, #b9e5f7 35%, #6693f4 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  #feffff 0%,#b9e5f7 35%,#6693f4 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  #feffff 0%,#b9e5f7 35%,#6693f4 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#feffff', endColorstr='#6693f4',GradientType=0 ); /* IE6-9 */

  }
  h1,h3{
    margin: 0 0 20px 0;
  }
  h1{
    color: red
  }
 .cardItem{
     cursor: pointer;
     width: 100%;
     height: 100%;
 }
 .cccc {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  padding-right: 10px;
  margin-bottom: 10px;
  height: 100px;
 }
 #12{
  height: 25px;
  width: 25px;
  background-color: rgb(0, 0, 0);
  border-radius: 50%;
  display: inline-block;
 }
 .cardC{
   padding: 0 !important;
 }
  .strike { position:relative;  width: 100%; height: fit-content; }
  .strike::after {
    content:' ';
    position:absolute;
    top:100%; left:0;
    width:0; height:100px;
    opacity:80%;
    transform:translateY(-50%);
    background:repeat-x url('./assets/strike.jpg');
    animation: strike 500ms linear 100ms 1 forwards;
  }
  @keyframes strike { to { width: 100%; } }
  .App-logo {
    height: 40vmin;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #09d3ac;
  }
  .bingoContainer{
      display: flex;
      flex-direction: column;

  }
  .stopBingo{
    z-index: 999;
  }
  .rewardContein{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  .rewardBg{
    height: 100vh;
    background-image: url("./assets//bg.png");
    
    background-repeat: repeat;
    background-size: cover;
  background-position: center center;
  }

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
body{
  font-size: 8px;
}
.cccc{
  height: 80px;
  padding: 5px;
}
}

/* Portrait */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: portrait) {
    body{
      font-size: 8px;
    }
    .cccc{
      height: 80px;
      padding: 5px;
    }
}

